<template>
    <div class="h-screen flex w-full bg-img vx-row no-gutter items-center justify-center">
        <div class="vx-col sm:w-1/2 md:w-1/2 lg:w-3/4 xl:w-3/5 sm:m-0 m-4">
            <vx-card>
                <div slot="no-body" class="full-page-bg-color">
                    <div class="vx-row no-gutter">
                        <div class="vx-col hidden sm:hidden md:hidden lg:block lg:w-1/2 mx-auto self-center">
                            <img src="@/assets/images/pages/register.jpg" alt="register" class="mx-auto">
                        </div>
                        <div class="vx-col sm:w-full md:w-full lg:w-1/2 mx-auto self-center  d-theme-dark-bg">
                            <div class="p-8">
                                <div class="vx-card__title">
                                    <h4 class="mb-4">Create Account</h4>
                                    <p>Fill the below form to create a new account.</p>
                                </div>
                                <div class="clearfix">

                                <div class="w-full mt-3">
                                  <label>{{$t("first_name")}}</label>
                                  <vs-input
                                          v-validate="'required'"
                                          data-vv-validate-on="blur"
                                          name="firstname"
                                          type="text"

                                          placeholder="first name"
                                          v-model="firstname"
                                          class="w-full" />
                                        <span class="text-danger text-sm">{{ errors.first('firstname') }}</span>
                                  </div>
                                   <div class="w-full mt-3">
                                     <label>{{$t("last_name")}}</label>
                                      <vs-input
                                        v-validate="'required'"
                                        data-vv-validate-on="blur"
                                        name="lastname"
                                        type="text"
                                        placeholder="last name"
                                        v-model="lastname"
                                        class="w-full" />
                                       <span class="text-danger text-sm">{{ errors.first('lastname') }}</span>
                                   </div>
                                  <div class="vx-row">

                                    <div class="vx-col md:w-1/2">
                                       <div class="mt-3">
                                         <label>date of birth</label>
                                        <datepicker placeholder="Select Date" v-model="date_of_birth"></datepicker>
                                     </div>

                                    </div>
                                      <div class="vx-col md:w-1/2 mt-3">
                                      <label>{{$t('tel')}}</label>
                                       <vs-input
                                        ref="tel"
                                        type="tel"
                                        data-vv-validate-on="tel"
                                        name="tel"
                                        placeholder="tel"
                                        v-model="tel"
                                        class="w-full" />


                                    </div>
                                  </div>
                                     <div class="w-full mt-3">
                                       <label>{{$t("address")}}</label>
                                       <vs-input
                                        ref="address"
                                        type="text"
                                        data-vv-validate-on="blur"

                                        name="address"
                                        placeholder="address"
                                        v-model="address"
                                        class="w-full" />
                                     </div>
                                      <div class="w-full mt-3">
                                          <lable>{{$t("choose_file")}}</lable>
                                         <vs-input type="file" class="w-full" @change="upload($event)"/>
                                      </div>
                                      <div class="w-full mt-3">
                                        <label>{{$t("username")}}</label>
                                         <vs-input
                                        v-validate="'required|min:3'"
                                        data-vv-validate-on="blur"
                                        name="username"
                                        placeholder="Username"
                                        v-model="username"
                                        class="w-full" />
                                       <span class="text-danger text-sm">{{ errors.first('username') }}</span>
                                      </div>

                                    <div class="w-full mt-3">
                                          <label>{{$t('password')}}</label>
                                      <vs-input
                                        ref="password"
                                        type="password"
                                        data-vv-validate-on="blur"
                                        v-validate="'required|min:6|max:10'"
                                        name="password"
                                        placeholder="Password"
                                        v-model="password"
                                        class="w-full mt-3" />
                                    <span class="text-danger text-sm">{{ errors.first('password') }}</span>
                                    </div>



                                    <div class="w-full mt-3">
                                      <label>{{$t("confirm_password")}}</label>
                                      <vs-input
                                        type="password"
                                        v-validate="'min:6|max:10|confirmed:password'"
                                        data-vv-validate-on="blur"
                                        data-vv-as="password"
                                        name="confirm_password"
                                        placeholder="Confirm Password"
                                        v-model="confirm_password"
                                        class="w-full" />
                                    <span class="text-danger text-sm">{{ errors.first('confirm_password') }}</span>
                                    </div>

                                    <vs-checkbox v-model="isTermsConditionAccepted" class="mt-3">I accept the terms & conditions.</vs-checkbox>
                                    <vs-button  type="border" to="/pages/login" class="mt-3">Login</vs-button>
                                    <vs-button class="float-right mt-3" @click="registerUser" :disabled="!validateForm">Register</vs-button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </vx-card>
        </div>
    </div>
</template>

<script>
import service from '@/service/service';
import Datepicker from 'vuejs-datepicker';
import moment from 'moment';
export default {
    data() {
        return {

            firstname:"",
            lastname:"",
            username:"",
            password:"",
            confirm_password:"",
            date_of_birth:"",
            address:"",
            tel:"",
            file_name:"",
            isTermsConditionAccepted: true,
        }
    },
    components:{
      Datepicker
    },
    computed: {
        validateForm() {
            return !this.errors.any() && this.username != ''&&this.first_name!='' && this.last_name != '' && this.password != '' && this.confirm_password != '' && this.isTermsConditionAccepted === true;
        }
    },
    methods: {
        registerUser() {
          if(this.validateForm){
            alert(this.file_name)
            service.postData('register',{
              username:this.username,
              first_name:this.firstname,
              last_name:this.lastname,
              password:this.password,
              date_of_birth:this.date_of_birth==""?"":moment(this.date_of_birth).format("YYYY-MM-DD HH:mm:ss"),
              address:this.address,
              tel:this.tel,
              file_name:this.file_name
            }).then((result)=>{
                this.$swal('data was registered','','success')
            },err=>{
              // this.$swal('connection error','','error')
            })
          }
        },
        upload(e){
          var file= e.target.files[0];
          if(file!=""){
              // console.log(e);
              let formData = new FormData();
              formData.append('file', file);
              service.upload("upload_file",formData).then(result=>{

                this.file_name=result.file_name;
                // this.$swal('file was uploaded','','success').then((result)=>{
                //   // this.forceRerender();
                // });
              })
          }
        },
        notifyAlreadyLogedIn() {
            this.$vs.notify({
                title: 'Login Attempt',
                text: 'You are already logged in!',
                iconPack: 'feather',
                icon: 'icon-alert-circle',
                color: 'warning'
            });
        },
    }
}
</script>
